import * as fs from "fs";
import { FileProvider } from "./FileProvider";

class Schema {
    protected fileProvider: FileProvider = new FileProvider();

    constructor(config: any) {
        this.initMembers(config);
    }

    initMembers(config: any) {}

    /**
     * 멤버 변수를 JSON 데이터로 변환합니다.
     */
    toJson(): string {
        return JSON.stringify(this, null, 2);
    }

    /**
     * 설정 파일을 로드하여 문자열로 반환합니다.
     *
     * @param filename
     * @returns
     */
    load(filename?: string): Promise<string> {
        if (!filename) {
            filename = this.constructor.name;
        }

        return new Promise((resolve, reject) => {
            this.fileProvider.readFile(filename!, "utf-8", (err, data) => {
                if (err) {
                    reject(err);
                    return;
                }

                resolve(data as string);
            });
        });
    }

    /**
     * 파일로 내보냅니다 (비동기 방식)
     *
     * @param filename
     */
    toFile(filename?: string): Promise<void> {
        const path = require("path");

        if (!filename) {
            filename = this.constructor.name;
        }

        const contents = this.toJson();

        return new Promise((resolve, reject) => {
            this.fileProvider.writeFile(
                filename!,
                contents,
                {
                    encoding: "utf8",
                },
                (err) => {
                    if (err) {
                        reject(err.message);
                    }
                    resolve();
                },
            );
        });
    }
}

export { Schema };
